import uniPopup from '@/components/uni-popup/uni-popup.vue';
export default {
	components: {
		uniPopup,
		uniIcon
	},
	data() {
		return {
			flag: true,
			type: '',
			addrMain: {},
			items: [],
			sumPrice: '0.00',
			headerPosition: 'fixed',
			headerTop: null,
			statusTop: null,
			selectedList: [],
			isAllselected: false,
			goodsList: [],
			//控制滑动效果
			theIndex: null,
			oldIndex: null,
			isStop: false
		};
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop >= 0 ? 'fixed' : 'absolute';
		this.headerTop = e.scrollTop >= 0 ? null : 0;
		this.statusTop = e.scrollTop >= 0 ? null : -this.statusHeight + 'px';
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {

		uni.getLocation({
			type: 'wgs84',
			success: res => {
				console.log('当前位置的经度：' + res.longitude);
				console.log('当前位置的纬度：' + res.latitude);
				this.temp(res);
			}
		});

		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	onLoad() {

		uni.getLocation({
			type: 'wgs84',
			success: res => {
				console.log('当前位置的经度：' + res.longitude);
				console.log('当前位置的纬度：' + res.latitude);
				this.temp(res);
			}
		});
		uni.login({
			provider: 'weixin',
			success: function(loginRes) {
				console.log(loginRes);
				console.log(loginRes.code);
				uni.request({
					url: 'https://tzs.yuanfudashi.com/wechat/getOpenIdXcx',
					data: {
						appid: 'wxc04f9bfb1157e062',
						secret: '9258da2f76f0de7ff1b3d740be743128',
						js_code: loginRes.code,
						grant_type: 'authorization_code'
					},
					method: 'GET',
					success: res => {
						console.log('热openId');
						console.log(res.data);
						console.log(res.data.openid);
						// console.log(res.data);
						// alert(res.data);
						uni.setStorageSync('openId', res.data.openid);
						uni.request({
							url: 'https://tzs.yuanfudashi.com/user/insert?wechatOpenid=' + uni.getStorageSync('openId'),
							// data: {
							// 	wechatOpenid: uni.getStorageSync('openId')
							// },
							method: 'POST',
							success: res => {}
						});
					}
				});
			}
		});









































		//兼容H5下结算条位置
		//





	},
	onBackPress() {
		if (this.type !== '') {
			this.type = '';
			return true;
		}
	},
	methods: {
		temp(res) {
			uni.showLoading({ title: '加载中' });
			uni.request({
				url: this.$tempUrl + 'shop/listByAddress',
				method: 'GET',
				data: {
					longitude: res.longitude,
					latitude: res.latitude,
					distance: 3000
				},
				success: res => {
					if (res.data.data.length === 0) {
						// uni.showModal({
						// 	title: '啊哦，加载失败',
						// 	content: '您附近暂无入驻商家哦>.<#，快去通知送水工入驻吧',
						// 	showCancel: false,
						// 	success: res => {
						// 		if (res.confirm) {
						// 			console.log('用户点击确定');
						// 			// uni.reLaunch({
						// 			// 	url: '../tabBar/cart'
						// 			// });
						// 		} else if (res.cancel) {
						// 			console.log('用户点击取消');
						// 		}
						// 	}
						// });
						// uni.showToast({ title: '啊哦，您附近暂无入驻商家>.<# 将为您显示所有商家列表', icon: 'none' });
						uni.request({
							url: this.$tempUrl + 'shop/list',
							method: 'GET',
							success: res => {
								// console.log(res.data.data);
								this.items = res.data.data;
								this.addrMain = this.items[0];
								uni.request({
									url: this.$tempUrl + 'product/list',
									data: { shopId: this.items[0].id },
									method: 'GET',
									success: res => {
										uni.hideLoading();
										// console.log(res.data.data);
										this.goodsList = res.data.data;
										if (res.data.data.length === 0) {
											uni.showToast({ title: '啊哦，此商家还未上架商品，换家点吧>.<#', icon: 'none' });
										}
									}
								});
							}
						});
					} else {
						this.items = res.data.data;
						this.addrMain = this.items[0];
						uni.request({
							url: this.$tempUrl + 'product/list',
							data: { shopId: this.items[0].id },
							method: 'GET',
							success: res => {
								uni.hideLoading();
								// console.log(res.data.data);
								this.goodsList = res.data.data;
								if (res.data.data.length === 0) {
									uni.showToast({ title: '啊哦，此商家还未上架商品，换家点吧>.<#', icon: 'none' });
								}
							}
						});
					}
				}
			});
		},
		addrChange(item) {
			console.log(item.id);
			this.type = '';
			this.addrMain = item;
			uni.request({
				url: this.$tempUrl + 'product/list',
				data: { shopId: item.id },
				method: 'GET',
				success: res => {
					console.log(res.data.data);
					this.goodsList = res.data.data;
					if (res.data.data.length === 0) {
						uni.showToast({ title: '啊哦，此商家还未上架商品，换家点吧>.<#', icon: 'none' });
					}
				}
			});
			// this.goodsList=[];
		},
		togglePopup(type) {
			this.type = type;
			// uni.showToast({ title: '这地方太偏啦，附近2公里内就这一家=。=', icon: 'none' });
		},
		//控制左滑删除效果-begin
		touchStart(index, event) {
			//多点触控不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			this.oldIndex = this.theIndex;
			this.theIndex = null;
			//初始坐标
			this.initXY = [event.touches[0].pageX, event.touches[0].pageY];
		},
		touchMove(index, event) {
			//多点触控不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			let moveX = event.touches[0].pageX - this.initXY[0];
			let moveY = event.touches[0].pageY - this.initXY[1];

			if (this.isStop || Math.abs(moveX) < 5) {
				return;
			}
			if (Math.abs(moveY) > Math.abs(moveX)) {
				// 竖向滑动-不触发左滑效果
				this.isStop = true;
				return;
			}

			if (moveX < 0) {
				this.theIndex = index;
				this.isStop = true;
			} else if (moveX > 0) {
				if (this.theIndex != null && this.oldIndex == this.theIndex) {
					this.oldIndex = index;
					this.theIndex = null;
					this.isStop = true;
					setTimeout(() => {
						this.oldIndex = null;
					}, 150);
				}
			}
		},
		touchEnd(index, $event) {
			//结束禁止触发效果
			this.isStop = false;
		},
		//控制左滑删除效果-end

		//商品跳转
		toGoods(e) {
			console.log('点击了商品' + e.id);
			// uni.showToast({ title: '商品' + e.id, icon: 'none' });
			// uni.navigateTo({
			// 	url: '../goods/goods'
			// });
		},
		//跳转确认订单页面
		toConfirmation() {
			let tmpList = [];
			let len = this.goodsList.length;
			for (let i = 0; i < len; i++) {
				if (this.goodsList[i].selected) {
					tmpList.push(this.goodsList[i]);
				}
			}
			if (tmpList.length < 1) {
				uni.showToast({
					title: '请选择商品结算',
					icon: 'none'
				});
				return;
			}
			uni.setStorageSync('shopId', this.addrMain.id);
			uni.setStorage({
				key: 'buylist',
				data: tmpList,
				success: () => {
					uni.navigateTo({
						url: '../order/confirmation',
						complete: () => {
							// console.log("热")
							uni.removeStorage({
								key: 'confirmCoupon',
								success: function(res) {
									// this.buylist = [];
									console.log('remove confirmCoupon success');
								}
							});
						}
					});
				}
			});
		},
		//删除商品
		deleteGoods(id) {
			let len = this.goodsList.length;
			for (let i = 0; i < len; i++) {
				if (id == this.goodsList[i].id) {
					this.goodsList.splice(i, 1);
					break;
				}
			}
			this.sum();
			this.oldIndex = null;
			this.theIndex = null;
		},
		// 删除商品s
		deleteList() {
			let len = this.selectedList.length;
			for (let i = 0; i < len; i++) {
				this.deleteGoods(this.selectedList[i]);
			}
			this.selectedList = [];
			this.isAllselected = this.selectedList.length == this.goodsList.length && this.goodsList.length > 0;
			this.sum();
		},
		// 选中商品
		selected(index) {
			this.goodsList[index].selected = this.goodsList[index].selected ? false : true;
			let i = this.selectedList.indexOf(this.goodsList[index].id);
			i > -1 ? this.selectedList.splice(i, 1) : this.selectedList.push(this.goodsList[index].id);
			this.isAllselected = this.selectedList.length == this.goodsList.length;
			this.sum();
		},
		//全选
		allSelect() {
			let len = this.goodsList.length;
			let arr = [];
			for (let i = 0; i < len; i++) {
				this.goodsList[i].selected = this.isAllselected ? false : true;
				arr.push(this.goodsList[i].id);
			}
			this.selectedList = this.isAllselected ? [] : arr;
			this.isAllselected = this.isAllselected || this.goodsList.length == 0 ? false : true;
			this.sum();
		},
		// 减少数量
		sub(index) {
			if (this.goodsList[index].stock <= 1) {
				return;
			}
			this.goodsList[index].stock--;
			this.sum();
		},
		// 增加数量
		add(index) {
			this.goodsList[index].stock++;
			this.sum();
		},
		// 合计
		sum() {
			this.sumPrice = 0;
			let len = this.goodsList.length;
			for (let i = 0; i < len; i++) {
				if (this.goodsList[i].selected) {
					this.sumPrice = this.sumPrice + this.goodsList[i].stock * this.goodsList[i].price;
				}
			}
			this.sumPrice = this.sumPrice.toFixed(2);
		},
		discard() {
			//丢弃
		},
		// 拨打电话
		toCall(phoneNumber) {
			uni.showModal({
				title: '提示',
				content: '是否拨打商家电话？',
				success: res => {
					if (res.confirm) {
						console.log('用户点击确定');
						uni.makePhoneCall({
							phoneNumber: phoneNumber
						});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		}
	}
};
